import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import {makeStyles} from '@mui/styles';
import { Collapse } from '@mui/material';
import {Link} from 'react-router-dom'

const useStyles = makeStyles({

  root: {
    width: 545,
    background: 'rgba(0, 0, 0, 0.5)!important',
    margin: ' 40px 40px',

  },
  media:{
    height: 440,
  },
  title:{
    fontFamily:'Nunito !important',
    fontWeight: 'bold !important',
    fontSize:'2rem !important',
    //textAlign: 'center',
    color:'#fff'
  }, 
  desc:{
    fontFamily:'Nunito !important',
    fontSize:'1.1rem !important',
    color:'#ddd !important',
    maxHeight:250,
  },
  bottom: {
      backgroundColor:'#5AFF3D!important',
      fontFamily:'Nunito !important',
      fontWeight: 'bold !important',
      fontSize:'0.9rem !important',
      color:'#000 !important',
      margin:'15px !important',
      padding:'0.5rem !important',
      textAlign: 'center',

  }

})

const detail =[
    {
        image: process.env.PUBLIC_URL + '/assets/image-canarie.jpeg',

    },
    {
        image: process.env.PUBLIC_URL + '/assets/image-italy.jpeg',

    },
    {
        image: process.env.PUBLIC_URL + '/assets/image-norvegia.jpeg',

    },
    {
        image: process.env.PUBLIC_URL + '/assets/image-ny.jpeg',

    }
]

export default function CardCategory({place, checked, index}) {
  const classes= useStyles();
  return (
    <Collapse in={checked}{...(checked ? { timeout: 1000 } : {})}>
      <Card className={classes.root}>
        <CardMedia
          className={classes.media}
          component="img"
          height="140"
          image={detail[index].image}
        />
        <CardContent>
          <Typography 
            gutterBottom 
            variant="h5" 
            component="h2" 
            className={classes.title}>
            {place.name}
          </Typography>
          <Typography variant="body2" color="text.secondary" component="p" className={classes.desc}
          dangerouslySetInnerHTML={{ __html: place.description }}
          >
            
          </Typography>
        </CardContent>
        <CardActions>
          <Button component={Link} to={`${place.id}`} className={classes.bottom} size="small" >Scopri</Button>
        </CardActions>
      </Card>
    </Collapse>
  );
}
