import './App.css';
import {Routes, Route} from 'react-router-dom'
import SignIn from './components/sign-in/SignIn'
import SignUp from './components/sign-in/SignUp'
import LandingPage from './components/LandingPage'
import LandingTo from './components/LandingTo'
import { PlaceProvider } from './containers/ContextComponent';
import { LoginProvider } from './containers/ContextLogin';
import CardAccesso from './components/card/CardAccesso'




function App() {

  return (
    <div className="App">
      <LoginProvider>
          <PlaceProvider>
            <Routes>
              <Route path="/blog-react-demo" element={<LandingPage/>}/>
              <Route path="/sign-in" element={<SignIn/>}/> 
              <Route path="/sign-up" element={<SignUp/>}/> 
              <Route path="/blog-react-demo/:id" element={<LandingTo/>}/> 
              <Route path="/blog-react-demo/dashboard" element={<CardAccesso/>}/> 
            </Routes>
          </PlaceProvider>
        </LoginProvider>
    </div>
  );
}

export default App;
