import React, {useEffect, useState} from 'react'
import {makeStyles} from '@mui/styles';
import { Collapse, IconButton} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Link  as Scroll} from 'react-scroll'
import MyAppbar from '../iconMenu/MyAppbar'
import axios from 'axios'
import { useParams } from 'react-router-dom';




const useStyles = makeStyles((theme) => ({
    root:{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',

    },
    appbar: {
        background:'none !important',
        fontFamily:'Nunito'
    },
    appbarTitle:{
        flexGrow:'1',
    },
    appbarWrapper: {
       width: '80%',
       margin: '0 auto'
    },
    icon:{  
        color:'#fff',
        fontSize: '2rem'
    },
    colorText: {
        color: '#5AFF3D'
    },
    container: {
        textAlign: 'center',
    },
    title:{
        color: '#fff',
        fontFamily:'Nunito',
        fontSize: '4.5rem',
    },
    goDown: {
        color: '#5AFF3D',
        fontSize: '4rem !important',
    },


}))

export default function MainTo({load}) {
    const classes = useStyles();
    const [checked, setChecked] = useState(false)
    const [place, setPlace] = useState();
    const [loaded, setLoaded] = useState(false)
    let param = useParams()



    useEffect(() =>{
        setChecked(true);
    },[]);


    useEffect(() => {
        let APIcategories = 'https://blog-react-demo.x10.mx/wp/wp-json/wp/v2/categories?parent=2'
        axios.get(APIcategories).then(response => {
            setPlace(response.data)
            setLoaded(true)
            

        })   
    },[])

    let id = []
    if (loaded=== true) {
        id = place.filter(el => +el.id === +param.id)
    }
  

    return (

        <div className={classes.root} id="header">
            <MyAppbar/>
            <Collapse 
                in={checked}
                {...(checked ? { timeout: 1000 } : {})}
                collapsedHeight={50}
            >
                <div className={classes.container}>
                    <h1 className={classes.title}>
                        Explore <br/> 
                    {loaded !== false ? (
                            <span className={classes.colorText}>{id[0].name}</span>    
                    ): '.'}
                    </h1>
                    <Scroll to="place-to-visit" smooth={true}>
                    <IconButton>
                    <ExpandMoreIcon className={classes.goDown}/>   
                    </IconButton>
                    </Scroll>

                    

                </div>
            </Collapse>

        </div>
    )
}
