import React , {useState, useEffect, useContext}  from 'react'
import {useNavigate} from 'react-router-dom'
import axios from 'axios'
import {makeStyles} from '@mui/styles';
import CardSignIn from '../card/CardSignIn';
import useWindowPosition from '../../hook/useWindowPosition'
import { LoginContext} from '../../containers/ContextLogin'

const useStyles = makeStyles((theme) => ({
    root:{
        minHeight:'100vh',
        display: 'flex',
        flexWrap:'wrap',
        flexDirection:'row',
        justifyContent: 'center',
        alignItems: 'center',
/*         [theme.breakpoints.down('md')]: {
            flexDirection: 'column',
        }, */
    },

}));

export default function PlaceToSignIn() {
    const classes = useStyles();
    const checked = useWindowPosition('header');
    
    const [login, setLogin]= useContext(LoginContext)
    const [user, setUser]= useState({})
    let navigate = useNavigate();

    
    let handleInput = (event) =>{
        let obj = Object.assign({}, user)
        obj[event.target.name] = event.target.value
        console.log(obj)
        setUser(obj);
    }


    useEffect(() =>{
        if (login ===[]) {
            navigate ('/blog-react-demo');
        }else{

            
        }
    }, [login, navigate])

     let signIn = () => {
        axios.post('https://blog-react-demo.x10.mx/wp/wp-json/jwt-auth/v1/token', user)
        .then(response => {
            console.log(response.data)
            setLogin(response.data)
            sessionStorage.setItem('token', response.data.token)
            sessionStorage.setItem('user_nicename', response.data.user_nicename)
            sessionStorage.setItem('user_email', response.data.user_email)
            sessionStorage.setItem('user_display_name', response.data.user_display_name)
        })
        .catch((error) => {
            console.log(error)
        })
    }

    return (
        <div className={classes.root} id="place-to-visit">
            <CardSignIn handleInput={handleInput} signIn={signIn} checked={checked} />
      </div>
    )
}
