import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { makeStyles } from "@mui/styles";
import { styled } from "@mui/material/styles";
import Collapse from "@mui/material/Collapse";
import FavoriteIcon from "@mui/icons-material/Favorite";
import ShareIcon from "@mui/icons-material/Share";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import IconButton from "@mui/material/IconButton";

const useStyles = makeStyles({
  root: {
    width: 545,
    background: "rgba(0, 0, 0, 0.5)!important",
    margin: " 40px 40px",
  },
  media: {
    height: 440,
  },
  title: {
    fontFamily: "Nunito !important",
    fontWeight: "bold !important",
    fontSize: "2rem !important",
    marginLeft: "15px !important",
    color: "#fff",
  },
  desc: {
    fontFamily: "Nunito !important",
    fontSize: "1.2rem !important",
    color: "#ddd !important",
  },
  bottom: {
    fontFamily: "Nunito !important",
    color: "#5AFF3D !important",
    fontSize: "2rem !important",
  },
  bottomExpand: {
    fontFamily: "Nunito !important",
    backgroundColor: "#5AFF3D !important",
    color: "#000 !important",
  },
});
const ExpandMore = styled((props) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? "rotate(0deg)" : "rotate(180deg)",
  marginLeft: "auto",
  transition: theme.transitions.create("transform", {
    duration: theme.transitions.duration.shortest,
  }),
}));

export default function CardIt({ place, checked }) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <Collapse in={checked} {...(checked ? { timeout: 1000 } : {})}>
      <Card className={classes.root}>
        <CardMedia
          className={classes.media}
          component="img"
          height="140"
          image={place.featured_media_src_url}
        />
        <CardContent>
          <Typography
            gutterBottom
            variant="h5"
            component="h2"
            className={classes.title}
          >
            {place.title.rendered}
          </Typography>
          <CardActions disableSpacing>
            <IconButton aria-label="add to favorites">
              <FavoriteIcon className={classes.bottom} />
            </IconButton>
            <IconButton aria-label="share">
              <ShareIcon className={classes.bottom} />
            </IconButton>
            <ExpandMore
              className={classes.bottomExpand}
              sx={{ fontSize: 40 }}
              expand={expanded}
              onClick={handleExpandClick}
              aria-expanded={expanded}
              aria-label="show more"
            >
              <ExpandMoreIcon />
            </ExpandMore>
          </CardActions>
          <Collapse in={expanded} timeout="auto" unmountOnExit>
            <CardContent>
              <Typography
                paragraph
                dangerouslySetInnerHTML={{ __html: place.content.rendered }}
                className={classes.desc}
              ></Typography>
            </CardContent>
          </Collapse>
        </CardContent>
      </Card>
    </Collapse>
  );
}
