import { CssBaseline } from '@mui/material';
import {makeStyles} from '@mui/styles';
import React from 'react'
import PlaceTo from './placeToVisit/PlaceTo'
import MainTo from './Main/MainTo';
import axios from 'axios'
import {useState, useEffect, useContext} from 'react'
import {useParams} from 'react-router-dom'
import { PlaceContext } from '../containers/ContextComponent';




const useStyles = makeStyles({
    root: { 
        minHeight: '100vh',
        backgroundImage: `url(${process.env.PUBLIC_URL + '/assets/bg-italia.jpeg'})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
    },
});





export default function LandingTo() {
    const classes = useStyles();
    const [place, setPlace] = useContext(PlaceContext);
    const params = useParams()

    const [load, setLoad] = useState(false)



    useEffect(() => {
        axios.get('https://blog-react-demo.x10.mx/wp/wp-json/wp/v2/posts?categories='+params.id).then (response => {
            setPlace(response.data)
            setLoad(true)    
            }) 
    },[params])
    


    return (
        <div className={classes.root}>
            <CssBaseline/>
            <MainTo load={load}/>
            <PlaceTo />

            
        </div>
    )
}
