import React from 'react'
import axios from 'axios'
import {useNavigate} from 'react-router-dom'
import {makeStyles} from '@mui/styles';
import CardSignUp from '../card/CardSignUp';
import useWindowPosition from '../../hook/useWindowPosition'
import {useState} from 'react'

const useStyles = makeStyles((theme) => ({
    root:{
        minHeight:'100vh',
        display: 'flex',
        flexWrap:'wrap',
        flexDirection:'row',
        justifyContent: 'center',
        alignItems: 'center',
/*         [theme.breakpoints.down('md')]: {
            flexDirection: 'column',
        }, */
    },

}));

export default function PlaceToSignUp() {
    const classes = useStyles();
    const checked = useWindowPosition('header');
    const [newUser, setNewUser]= useState({})
    let navigate = useNavigate();


    let handleNewUser = (event) =>{
        let obj = Object.assign({}, newUser);
        obj[event.target.name] = event.target.value;
        setNewUser(obj)
        console.log(obj)
    }

    let signUp = () =>{
        axios.post('https://blog-react-demo.x10.mx/wp/wp-json/wp/v2/users/register',newUser)
        .then( function( response ) {
            console.log( response.data)
            navigate("/blog-react-demo")    
        } )
    }
    // recuperare dati utenti loggato
    // wp-json/v2/users/me + headers:'Authorization', 'Bearer+token'

    return (
        <div className={classes.root} id="place-to-visit">
            <CardSignUp handleNewUser={handleNewUser} signUp={signUp} checked={checked} />
      </div>
    )
}
